.main-header {
  width: 320px;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    width: 708px;
  }

  @media (min-width: @desktop-width) {
    width: 1440px;
    min-height: 112px;
  }
}

.main-header__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 65px;
  padding: 0 20px;

  @media (min-width: @tablet-width) {
    margin: 0;
    padding: 0 30px;
    justify-content: start;
    min-height: 78px;
  }

  @media (min-width: @desktop-width) {
    padding: 0 110px;
  }
}

.main-header__logo {
  @media (min-width: @tablet-width) {
    margin-right: 12px;
    margin-top: 25px;
  }

  @media (min-width: @desktop-width) {
    margin-right: 14px;
  }
}

.main-header__logo-title {
  margin-right: 89px;

  @media (min-width: @tablet-width) {
    margin-right: 93px;
    margin-top: 43px;
  }

  @media (min-width: @desktop-width) {
    margin-right: auto;
  }
}
